import React from 'react'
import ReactDOM from 'react-dom'
import AppConnect from './connectors/App.connect'
import * as serviceWorker from './serviceWorker'
import log from 'loglevel'

// Here's our global overrides css.
// NOTE: we don't use the JSS compilations here because mapbox feaks
// out even though it's probably fine
import '@morphoto/themes/baseline.css'
import 'mapbox-gl/dist/mapbox-gl.css'
import { devSetup } from '@morphoto/client-common'
// This is the font we need
require('typeface-roboto')

// This does its own process.env.NODE_ENV check
// In production it only presents a minimal set of tools
devSetup()

ReactDOM.render(<AppConnect />, document.getElementById('root'))

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.register()

// On android this will trick the back button into not closing the app
window.addEventListener('load', () => {
  window.history.pushState({}, '')
})

// This is our image worker to offline cache maptiles AND series photos
if (navigator.serviceWorker) {
  navigator.serviceWorker.register('imgCacheWorker.js')
}

// In Chrome > 52 we need to explicitly request persistent storage to make sure our
// stuff isn't cleaned up automatically.
if (navigator.storage && navigator.storage.persist) {
  navigator.storage.persist().then(granted => {
    if (granted) {
      log.info('Storage will not be cleared except by explicit user action')
    }
    else {
      log.warn('Storage may be cleared by the UA under storage pressure.')
    }
  })
}
else {
  log.warn('navigator.storage API not implemented.')
}
